import React, { useState } from "react";
import { data } from "../data/managementStationList";
import double_prev from "../images/double_prev.png";
import prev from "../images/prev.png";
import next from "../images/next.png";
import double_next from "../images/double_next.png";

function ManagemnetStationList() {
  const [currentPage, setcurrentPage] = useState(1);
  const [itemsPerPage] = useState(25);

  const [pageNumberLimit] = useState(10);
  const [maxPageNumberLimit, setmaxPageNumberLimit] = useState(10);
  const [minPageNumberLimit, setminPageNumberLimit] = useState(0);

  const rest = data.length % itemsPerPage;
  if (rest !== 0) {
    for (let i = rest; i < itemsPerPage; i++) {
      data.push({});
    }
  }

  const handleClick = (event) => {
    setcurrentPage(Number(event.target.id));
  };

  const pages = [];
  for (let i = 1; i <= Math.ceil(data.length / itemsPerPage); i++) {
    pages.push(i);
  }

  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;
  const currentItems = data.slice(indexOfFirstItem, indexOfLastItem);

  const renderPageNumbers = pages.map((number) => {
    if (number < maxPageNumberLimit + 1 && number > minPageNumberLimit) {
      return (
        <li
          key={number}
          id={number}
          onClick={handleClick}
          className={currentPage === number ? "active" : null}
        >
          {number}
        </li>
      );
    } else {
      return null;
    }
  });

  const stationList = currentItems.map((data, index) => (
    <tr key={index} className={data.stationNumber ? "" : "empty"}>
      <td>{data.stationNumber}</td>
      <td>{data.stationName}</td>
      <td>{data.installAddress}</td>
      <td>{data.portCount}</td>
      <td>
        {((type) => {
          if (type === "0") {
            return "태양광";
          } else if (type === "1") {
            return "수전";
          }
        })(data.chargeType)}
      </td>
      <td>{data.administrator}</td>
      <td>
        {((state) => {
          if (state === "0") {
            return "정지";
          } else if (state === "1") {
            return "운용 중";
          } else if (state === "2") {
            return "수리 중";
          } else if (state === "3") {
            return "에러";
          }
        })(data.state)}
      </td>
    </tr>
  ));

  const handleDoublePrevbtn = () => {
    if (currentPage <= pageNumberLimit) {
      setcurrentPage(1);
    } else {
      setcurrentPage(currentPage - pageNumberLimit);
      setmaxPageNumberLimit(maxPageNumberLimit - pageNumberLimit);
      setminPageNumberLimit(minPageNumberLimit - pageNumberLimit);
    }
  };

  const handlePrevbtn = () => {
    setcurrentPage(currentPage - 1);

    if ((currentPage - 1) % pageNumberLimit === 0) {
      setmaxPageNumberLimit(maxPageNumberLimit - pageNumberLimit);
      setminPageNumberLimit(minPageNumberLimit - pageNumberLimit);
    }
  };

  const handleNextbtn = () => {
    setcurrentPage(currentPage + 1);

    if (currentPage + 1 > maxPageNumberLimit) {
      setmaxPageNumberLimit(maxPageNumberLimit + pageNumberLimit);
      setminPageNumberLimit(minPageNumberLimit + pageNumberLimit);
    }
  };

  const handleDoubleNextbtn = () => {
    if (currentPage + pageNumberLimit > pages.length) {
      setcurrentPage(pages.length);
      if (
        parseInt(currentPage / pageNumberLimit) !==
        parseInt(pages.length / pageNumberLimit)
      ) {
        setmaxPageNumberLimit(maxPageNumberLimit + pageNumberLimit);
        setminPageNumberLimit(minPageNumberLimit + pageNumberLimit);
      }
    } else {
      setcurrentPage(currentPage + pageNumberLimit);
      setmaxPageNumberLimit(maxPageNumberLimit + pageNumberLimit);
      setminPageNumberLimit(minPageNumberLimit + pageNumberLimit);
    }
  };

  return (
    <div className="managementStationList">
      <table className="managementStation_table">
        <thead>
          <tr>
            <th>스테이션 번호</th>
            <th>스테이션 명</th>
            <th>설치 주소</th>
            <th>포트 수</th>
            <th>발전 방식</th>
            <th>관리자</th>
            <th>운영상태</th>
          </tr>
        </thead>
        <tbody>{stationList}</tbody>
      </table>
      <div className="pagination">
        <button
          onClick={handleDoublePrevbtn}
          disabled={currentPage === pages[0] ? true : false}
          className="double_prev"
        >
          <img src={double_prev} alt="double_prev"></img>
        </button>
        <button
          onClick={handlePrevbtn}
          disabled={currentPage === pages[0] ? true : false}
          className="prev"
        >
          <img src={prev} alt="prev"></img>
        </button>
        {renderPageNumbers}
        <button
          onClick={handleNextbtn}
          disabled={currentPage === pages[pages.length - 1] ? true : false}
          className="next"
        >
          <img src={next} alt="next"></img>
        </button>
        <button
          onClick={handleDoubleNextbtn}
          disabled={currentPage === pages[pages.length - 1] ? true : false}
          className="double_next"
        >
          <img src={double_next} alt="double_next"></img>
        </button>
      </div>
    </div>
  );
}

export default ManagemnetStationList;
