import Header from "../../components/Header";
import EntireNav from "../../components/EntrieNav";
import SortSelect from "../../components/SortSelect";
import Search from "../../components/Search";
import SearchButton from "../../components/SearchButton";
import AddButton from "../../components/AddButton";
import ManagementStationList from "../../components/ManagementStationList";

function StationList() {
  const onClick = (e) => {
    let nWidth = "439";
    let nHeight = "716";

    // 듀얼 모니터 고려한 윈도우 띄우기
    let curX = window.screenLeft;
    let curY = window.screenTop;
    let curWidth = document.body.clientWidth;
    let curHeight = document.body.clientHeight;

    let nLeft = curX + curWidth / 2 - nWidth / 2;
    let nTop = curY + curHeight / 2 - nHeight / 2;

    let option = `top=${nTop}, left=${nLeft}, width=${nWidth}, height=${nHeight}`;

    window.open(
      "https://admin.wingstation.co.kr/station/register",
      "addStation",
      option
    );
  };

  return (
    <>
      <Header />
      <EntireNav code="/station" />
      <div>
        <div
          style={{ display: "flex", marginTop: "24px", marginBottom: "9px" }}
        >
          <div style={{ marginRight: "30px" }}>
            <SortSelect />
          </div>
          <div style={{ marginRight: "8px" }}>
            <Search />
          </div>
          <div style={{ marginRight: "auto" }}>
            <SearchButton />
          </div>
          <AddButton onClick={onClick} />
        </div>
        <ManagementStationList />
      </div>
    </>
  );
}

export default StationList;
